"use client"

import { useEffect, useState } from "react"
import { createPortal } from "react-dom"

type Status = "idle" | "success" | "error"

function QuoteForm() {
  const [status, setStatus] = useState<Status>("idle")
  const [message, setMessage] = useState<string>("")
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    setStatus("idle")
    setMessage("")
    setIsSubmitting(true)

    const form = event.currentTarget
    const formData = new FormData(form)

    try {
      const response = await fetch("/api/quote", {
        method: "POST",
        body: formData,
      })

      const payload = (await response.json()) as { ok?: boolean; error?: string }

      if (!response.ok || !payload.ok) {
        throw new Error(payload.error ?? "Unable to send email right now.")
      }

      form.reset()
      setStatus("success")
      setMessage("Message sent! We'll reply within one business day.")
    } catch (error) {
      setStatus("error")
      setMessage(error instanceof Error ? error.message : "Submission failed, please try again later.")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <form
      id="quote-form"
      className="elementor-form"
      name="quote-form"
      encType="multipart/form-data"
      onSubmit={handleSubmit}
    >
      <input type="hidden" name="source" value="Website Quote Form" />
      <div className="elementor-form-fields-wrapper elementor-labels-above">
        <div className="elementor-field-type-text elementor-field-group elementor-column elementor-field-group-name elementor-col-100 elementor-field-required">
          <input
            className="elementor-field elementor-size-sm elementor-field-textual"
            type="text"
            name="name"
            placeholder="Your Name*"
            required
            aria-required="true"
          />
        </div>
        <div className="elementor-field-type-text elementor-field-group elementor-column elementor-field-group-company elementor-col-100">
          <input
            className="elementor-field elementor-size-sm elementor-field-textual"
            type="text"
            name="company"
            placeholder="Company / Project"
          />
        </div>
        <div className="elementor-field-type-text elementor-field-group elementor-column elementor-field-group-phone elementor-col-100">
          <input
            className="elementor-field elementor-size-sm elementor-field-textual"
            type="text"
            name="phone"
            placeholder="Phone"
          />
        </div>
        <div className="elementor-field-type-email elementor-field-group elementor-column elementor-field-group-email elementor-col-100 elementor-field-required">
          <input
            className="elementor-field elementor-size-sm elementor-field-textual"
            type="email"
            name="email"
            placeholder="Email*"
            required
            aria-required="true"
          />
        </div>
        <div className="elementor-field-type-textarea elementor-field-group elementor-column elementor-field-group-message elementor-col-100 elementor-field-required">
          <textarea
            className="elementor-field-textual elementor-field elementor-size-sm"
            name="message"
            rows={4}
            placeholder="Your Message*"
            required
            aria-required="true"
          ></textarea>
        </div>
        <div className="elementor-field-type-upload elementor-field-group elementor-column elementor-field-group-fileupload elementor-col-100">
          <input
            className="elementor-field elementor-size-sm elementor-upload-field"
            type="file"
            name="attachment"
            aria-label="Upload reference file (optional)"
          />
        </div>
        <div className="elementor-field-group elementor-column elementor-field-type-submit elementor-col-100 e-form__buttons">
          <button className="elementor-button elementor-size-sm" type="submit" disabled={isSubmitting}>
            <span className="elementor-button-content-wrapper">
              <span className="elementor-button-text">
                {isSubmitting ? "Sending..." : status === "success" ? "Sent!" : "Get A Quote Now"}
              </span>
            </span>
          </button>
        </div>
        {message && (
          <div className="elementor-field-group elementor-col-100 text-sm" role="status">
            <span style={{ color: status === "error" ? "#b91c1c" : "#0f9d58" }}>{message}</span>
          </div>
        )}
      </div>
    </form>
  )
}

export default function QuoteFormPortal() {
  const [host, setHost] = useState<HTMLElement | null>(null)

  useEffect(() => {
    setHost(document.getElementById("quote-form-host") as HTMLElement | null)
  }, [])

  if (!host) {
    return null
  }

  return createPortal(<QuoteForm />, host)
}

